import { encodePath } from '@nextcloud/paths';
import { generateOcsUrl } from '@nextcloud/router';
import { getCurrentUser } from '@nextcloud/auth';
import { FileAction, Permission } from '@nextcloud/files';
import { showError, DialogBuilder } from '@nextcloud/dialogs';
import { translate as t } from '@nextcloud/l10n';
import axios from '@nextcloud/axios';
import LaptopSvg from '@mdi/svg/svg/laptop.svg?raw';
import IconCancel from '@mdi/svg/svg/cancel.svg?raw';
import IconCheck from '@mdi/svg/svg/check.svg?raw';
import { isPublicShare } from '@nextcloud/sharing/public';
const confirmLocalEditDialog = function () {
  let localEditCallback = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : () => {};
  let callbackCalled = false;
  return new DialogBuilder().setName(t('files', 'Edit file locally')).setText(t('files', 'The file should now open locally. If you don\'t see this happening, make sure that the desktop client is installed on your system.')).setButtons([{
    label: t('files', 'Retry local edit'),
    icon: IconCancel,
    callback: () => {
      callbackCalled = true;
      localEditCallback(false);
    }
  }, {
    label: t('files', 'Edit online'),
    icon: IconCheck,
    type: 'primary',
    callback: () => {
      callbackCalled = true;
      localEditCallback(true);
    }
  }]).build().show().then(() => {
    // Ensure the callback is called even if the dialog is dismissed in other ways
    if (!callbackCalled) {
      localEditCallback(false);
    }
  });
};
const attemptOpenLocalClient = async path => {
  openLocalClient(path);
  confirmLocalEditDialog(openLocally => {
    if (!openLocally) {
      window.OCA.Viewer.open({
        path
      });
      return;
    }
    openLocalClient(path);
  });
};
const openLocalClient = async function (path) {
  const link = generateOcsUrl('apps/files/api/v1') + '/openlocaleditor?format=json';
  try {
    const result = await axios.post(link, {
      path
    });
    const uid = getCurrentUser()?.uid;
    let url = `nc://open/${uid}@` + window.location.host + encodePath(path);
    url += '?token=' + result.data.ocs.data.token;
    window.open(url, '_self');
  } catch (error) {
    showError(t('files', 'Failed to redirect to client'));
  }
};
export const action = new FileAction({
  id: 'edit-locally',
  displayName: () => t('files', 'Edit locally'),
  iconSvgInline: () => LaptopSvg,
  // Only works on single files
  enabled(nodes) {
    // Only works on single node
    if (nodes.length !== 1) {
      return false;
    }
    // does not work with shares
    if (isPublicShare()) {
      return false;
    }
    return (nodes[0].permissions & Permission.UPDATE) !== 0;
  },
  async exec(node) {
    attemptOpenLocalClient(node.path);
    return null;
  },
  order: 25
});