import { davRemoteURL } from '@nextcloud/files';
import axios from '@nextcloud/axios';
import { generateOcsUrl } from '@nextcloud/router';
import { getCurrentUser } from '@nextcloud/auth';
import { dirname, encodePath, joinPaths } from '@nextcloud/paths';
import { getCanonicalLocale, getLanguage } from '@nextcloud/l10n';
import { getContents as getFiles } from "./Files.ts";
export const folderTreeId = 'folders';
export const sourceRoot = `${davRemoteURL}/files/${getCurrentUser()?.uid}`;
const collator = Intl.Collator([getLanguage(), getCanonicalLocale()], {
  numeric: true,
  usage: 'sort'
});
const compareNodes = (a, b) => collator.compare(a.displayName ?? a.basename, b.displayName ?? b.basename);
const getTreeNodes = function (tree) {
  let currentPath = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : '/';
  let nodes = arguments.length > 2 && arguments[2] !== undefined ? arguments[2] : [];
  const sortedTree = tree.toSorted(compareNodes);
  for (const {
    id,
    basename,
    displayName,
    children
  } of sortedTree) {
    const path = joinPaths(currentPath, basename);
    const source = `${sourceRoot}${path}`;
    const node = {
      source,
      encodedSource: encodeSource(source),
      path,
      fileid: id,
      basename
    };
    if (displayName) {
      node.displayName = displayName;
    }
    nodes.push(node);
    if (children.length > 0) {
      getTreeNodes(children, path, nodes);
    }
  }
  return nodes;
};
export const getFolderTreeNodes = async function () {
  let path = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : '/';
  let depth = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : 1;
  const {
    data: tree
  } = await axios.get(generateOcsUrl('/apps/files/api/v1/folder-tree'), {
    params: new URLSearchParams({
      path,
      depth: String(depth)
    })
  });
  const nodes = getTreeNodes(tree, path);
  return nodes;
};
export const getContents = path => getFiles(path);
export const encodeSource = source => {
  const {
    origin
  } = new URL(source);
  return origin + encodePath(source.slice(origin.length));
};
export const getSourceParent = source => {
  const parent = dirname(source);
  if (parent === sourceRoot) {
    return folderTreeId;
  }
  return encodeSource(parent);
};